import Head from "../components/head";
import Navbar from "../components/Navbar";

export default () => (
  <div>
    <Head />
    <Navbar />

    <div class="container">
      <h1>Location and getting there</h1>
      <p>
        <a href="https://goo.gl/maps/BJvCVrDNGsC2">Location on Google Maps</a>
      </p>
      <h5>From the main train station</h5>
      <p>
        Take the "S-Bahn" to Rennweg (it leaves every ~5 mins from platform 2)<br />
        Don't take any of the "westbahn" trains (the blue-green-white ones) as
        those are more expensive.<br />Or take a taxi for ~12€
      </p>
      <h5>From Erdberg bus terminal</h5>
      <p>
        Take the U3 underground to "Landstraße". Then take the "S-Bahn" to
        Rennweg: Take any train that leaves from platform 1 or 2 except for the
        "westbahn" trains (the blue-green-white ones). Rennweg is the next
        station.
        <br />Or take a taxi for ~8 €
      </p>
      <h5>From the airport</h5>
      <p>
        There are trains running from the airport to the city. The easiest
        option is the S-Bahn S7, which will take you directly to the Rennweg
        station. This train runs twice per hour, at :18 and at :48. Other trains
        run at :03 and :03 - with these trains you'll have to change trains at
        the main train Station (Wien Hauptbahnhof) A ticket will cost you 3.90
        €.
        <br />Or take a taxi for ~25€
      </p>
      <h5>Finding the apartment</h5>
      <p>
        If you're at the Rennweg station, go up the escalators (not the stairs).
        You'll be near a bigger intersection. On the opposite side of the
        intersection there's a yellow fast food noodles stand. Close to it
        you'll see a "BIPA" store. Pass the BIPA store on the right and you'll
        be on the right street (Aspangstraße) - The apartment is 200 m down the
        street, at Aspangstraße 15, apartment 3.
      </p>
      <h5>Getting around</h5>
      <p>
        Vienna has a very good public transport system. You can buy day passes
        at the S-Bahn station or any underground station. Single tickets are
        also available on trams and buses.
      </p>
      <p>
        To get to / from the apartment you'll maily use the S-Bahn train or tram
        71. <br />The S-Bahn can take you to the central station, the main train
        station and the station "Prater".<br />
        Tram 71 will get you directly to the inner district and take you along
        the famous ring road where you can just hop off to see one of the many
        famous sights.
      </p>
    </div>
  </div>
);



